onForm\">\n                    <div>\n                        <JetLabel for=\"name\" value=\"Name\" />\n                        <JetInput\n                            id=\"name\"\n                            v-model=\"form.name\"\n                            type=\"text\"\n                            class=\"mt-1 block w-full\"\n                            required\n                            autofocus\n                            autocomplete=\"name\"\n                        />\n                    </div>\n\n                    <div class=\"mt-4\">\n                        <JetLabel for=\"email\" value=\"Email\" />\n                        <JetInput\n                            id=\"email\"\n                            v-model=\"form.email\"\n                            type=\"email\"\n                            class=\"mt-1 block w-full\"\n                            required\n                        />\n                    </div>\n\n                    <div class=\"mt-4\">\n                        <JetLabel for=\"password\" value=\"Password\" />\n                        <JetInput\n                            id=\"password\"\n                            v-model=\"form.password\"\n                            type=\"password\"\n                            class=\"mt-1 block w-full\"\n                            required\n                            autocomplete=\"new-password\"\n                        />\n                    </div>\n\n                    <div class=\"mt-4\">\n                        <JetLabel for=\"password_confirmation\" value=\"Confirm Password\" />\n                        <JetInput\n                            id=\"password_confirmation\"\n                            v-model=\"form.password_confirmation\"\n                            type=\"password\"\n                            class=\"mt-1 block w-full\"\n                            required\n                            autocomplete=\"new-password\"\n                        />\n                    </div>\n\n                    <div class=\"mt-4\">\n                        <JetLabel for=\"invite_code\" value=\"Invite Code\" class=\"text-green-800 font-semibold\" />\n                        <JetInput\n                            id=\"invite_code\"\n                            v-model=\"form.invite_code\"\n                            type=\"invite_code\"\n                            class=\"mt-1 block w-full text-black text-xl h-14 px-4 border-2 border-gray-300 focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50 rounded-md shadow-sm\"\n                            required\n                        />\n                    </div>\n\n                    <!--            Jetstream/Fortify Multi-Auth: Roles, Permissions and Guards-->\n                    <!--            https://www.youtube.com/watch?v=NiQSNjWKLfU-->\n\n                    <!--            <div class=\"mt-4\">-->\n                    <!--                <JetLabel  for=\"role_id\" value=\"Register as\" />-->\n                    <!--                <select name=\"role_id\" v-model=\"form.role_id\" class=\"mt-1 w-full border-gray-300 focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50 rounded-md shadow-sm\">-->\n                    <!--                    <option value=\"2\" selected>Viewer</option>-->\n                    <!--                    <option value=\"3\">Creator</option>-->\n                    <!--                </select>-->\n                    <!--            </div>-->\n\n                    <!--            <div class=\"mt-4\" v-if=\"form.role_id == 3\">-->\n                    <!--                <JetLabel for=\"user_phone\" value=\"Phone Number\" />-->\n                    <!--                <JetInput-->\n                    <!--                    id=\"user_phone\"-->\n                    <!--                    v-model=\"form.user_phone\"-->\n                    <!--                    type=\"phone\"-->\n                    <!--                    class=\"mt-1 block w-full\"-->\n                    <!--                    required-->\n                    <!--                    autocomplete=\"new-password\"-->\n                    <!--                />-->\n                    <!--            </div>-->\n\n\n                    <div v-if=\"$page.props.jetstream.hasTermsAndPrivacyPolicyFeature\" class=\"mt-4\">\n                        <JetLabel for=\"terms\">\n                            <div class=\"flex items-center\">\n                                <JetCheckbox id=\"terms\" v-model=\"form.terms\" name=\"terms\" required />\n\n                                <div class=\"ml-2\">\n                                    I agree to the <a :href=\"route('terms.show')\" target=\"_blank\" class=\"underline text-sm text-gray-600 hover:text-gray-900\">Terms of Service</a> and <a :href=\"route('policy.show')\" target=\"_blank\" class=\"underline text-sm text-gray-600 hover:text-gray-900\">Privacy Policy</a>\n                                </div>\n                            </div>\n                        </JetLabel>\n                    </div>\n\n                    <div class=\"flex items-center justify-end mt-4\">\n<!--                        <button @click=\"showLogin\" class=\"underline text-sm text-gray-600 hover:text-gray-900\">-->\n<!--                            Already registered?-->\n<!--                        </button>-->\n\n                        <JetButton class=\"ml-4\"  v-on:keyup.enter=\"submit\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">\n                            Register\n                        </JetButton>\n                    </div>\n                </form>\n                </div>\n\n            <footer>\n                <div class=\"modal-footer\">\n                    <button\n                        @click=\"clearForm\"\n                        class=\"bg-gray-300 p-2 rounded-md hover:bg-gray-400 hover:text-gray-800\"\n                    >Cancel</button>\n                </div>\n            </footer>\n            </div>\n        </div>\n    </Transition>\n</template>\n\n<script setup>\nimport { useWelcomeStore } from \"@/Stores/WelcomeStore\";\nimport { Link, useForm } from '@inertiajs/inertia-vue3';\nimport JetAuthenticationCardLogo from '@/Jetstream/AuthenticationCardLogo.vue';\nimport JetButton from '@/Jetstream/Button.vue';\nimport JetInput from '@/Jetstream/Input.vue';\nimport JetCheckbox from '@/Jetstream/Checkbox.vue';\nimport JetLabel from '@/Jetstream/Label.vue';\nimport JetValidationErrors from '@/Jetstream/ValidationErrors.vue';\n\nlet welcomeStore = useWelcomeStore()\n\ndefineProps({\n    status: String,\n    show: Boolean\n});\n\nconst form = useForm({\n    name: '',\n    email: '',\n    password: '',\n    password_confirmation: '',\n    terms: true,\n    invite_code: '',\n});\n\nfunction clearForm() {\n    form.reset();\n    welcomeStore.showRegister = false;\n}\nconst submit = () => {\n    form.post(route('register'), {\n        onFinish: () => form.reset('password', 'password_confirmation'),\n    });\n};\n\nfunction showLogin() {\n    form.reset();\n    welcomeStore.showLogin = true;\n    welcomeStore.showRegister = false;\n}\n\n</script>\n\n<style scoped>\n.modal-mask {\n    position: fixed;\n    inset: 0;\n    background: rgba(0,0,0,0.6);\n    display: grid;\n    place-items: center;\n    z-index: 200;\n}\n.modal-container {\n    background: white;\n    color: black;\n    padding: 1rem;\n    width: 80vw;\n    max-width: 500px;\n    border-radius: 8px;\n}\n.modal-footer {\n    border-top: 1px solid #ddd;\n    margin-top: 1rem;\n    padding-top: 0.5rem;\n    font-size: .8rem;\n}\n</style>\n"],"sourceRoot":""}]);
